<style lang="less" scoped src="./index.less" ></style>
<style lang="less">
body {
  background-color: #fafafc;
}
.contract {
  text-align: left;
  font-size: 15px;
  text-indent: 35px;
  .title {
    text-align: center;
    font-size: 24px;
  }
  .row_center {
    text-align: left;
    width: 100%;
  }
  .display {
    display: inline-block;
    width: 40%;
    margin-top: 10px;
  }
  .margin-30px {
    margin: 40px 0;
  }
  .margin-t-20px {
    margin-top: 20px;
  }
}

.purchaseContract {
  .el-form-item__label {
    float: none;
  }
  .el-dialog__body {
    padding-top: 0;
  }
  .el-dialog {
    width: 63%;
    top: -10%;
  }
  .el-table--border,
  .el-table--group {
    margin-top: 5px;
  }
  .el-table th {
    padding: 8px 0px;
    font-size: 14px;
    color: #333;
  }
  .el-table td {
    padding: 0;
    color: #333;
  }
  .el-textarea__inner {
    display: block;
    resize: vertical;
    padding: 5px 15px;
    line-height: 1.5;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    width: 100%;
    height: 100px;
    font-size: inherit;
    color: #606266;
    background-color: #fff;
    background-image: none;
    border: 1px solid #dcdfe6;
    border-radius: 4px;
    -webkit-transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
    transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  }
  .printExcel {
    display: none;
  }
}
.contract {
  .underline {
    text-decoration: underline;
    display: inline;
    padding: 0 5px;
  }
}
</style>
<template>
  <div class='purchaseContract project'>
    <div class='title'>
      <div class='left'>
        <div class="tab-block">
          <div class='text1'></div>
          <div class="tab-block-inner">
            <el-input v-model="searchObj.search" class='query' placeholder="/"></el-input>
            <el-button @click="search" icon="el-icon-search" circle></el-button>
            <!-- <el-button type="primary" @click='showAdd(0)'></el-button> -->
          </div>
        </div>
        <div>
          <el-button class='tip' :class="tipActiveIndex==0?'active':''" type="text" @click="clickTip(0)"></el-button>
          <el-button class='tip' :class="tipActiveIndex==1?'active':''" type="text" @click="clickTip(1)"></el-button>
        </div>
      </div>
    </div>
    <div>
      <contract-examine v-if="searchObj.tag_search == 0" :pageSize="pageSize" :page="searchObj.page" :tableData="tableData" @examine='examine' @printing='printing'></contract-examine>
      <maintain-list v-if="searchObj.tag_search == 1" :pageSize="pageSize" :page="searchObj.page" :tableData="tableData" @examine='examine' @tart='tart' @printing='printing'></maintain-list>
      <el-pagination background class='page' layout="prev, pager, next" :page-size="pageSize" :current-page="searchObj.page" :total="total" @current-change="pageChange">
      </el-pagination>
    </div>

    <div class="printExcel">
      <div class='contract'>
        <div class='title'></div>
        <div>
          <span class="underline"> {{test.name}} </span> 
          <span class="underline"> {{test.name}} </span>
        </div>
        <div>
          <span class="underline"> {{test.name}} </span> 
          <span class="underline"> {{test.name}} </span>
        </div>
        <div></div>
        <div></div>
        <div>1xxxxxxxxxxxxxxxxx</div>
        <div>2</div>
        <div>2.1
          <span class="underline"> {{test.name}} </span> 
          <span class="underline"> {{test.name}} </span>
          <span class="underline"> {{test.name}} </span>/
          <span class="underline"> {{test.name}} </span>
          <span class="underline"> {{test.name}} </span>/
          <span class="underline"> {{test.name}} </span>
          <span class="underline"> {{test.name}} </span>
        </div>
        <div>
          <span class="underline"> {{test.name}} </span>
          <span class="underline"> {{test.name}} </span>
          <span class="underline"> {{test.name}} </span></div>
        <div>2.2
          <span class="underline"> {{test.name}} </span> </div>
        <div>2.3
          <span class="underline"> {{test.name}} </span>/ 
          <span class="underline"> {{test.name}} </span> 
          <span class="underline"> {{test.name}} </span></div>
        <div>2.4</div>
        <div></div>
        <div>1</div>
        <div>2</div>
        <div></div>
        <div>1</div>
        <div>2</div>
        <div>3</div>
        <div>4</div>
        <div>5</div>
        <div>6</div>
        <div></div>
        <div>1
          <span class="underline"> {{test.name}} </span></div>
        <div>2</div>
        <div>3</div>
        <div>4</div>
        <div>5</div>
        <div>6 </div>
        <div></div>
        <div>1
          <span class="underline"> {{test.name}} </span></div>
        <div>2</div>
        <div>3
          <span class="underline"> {{test.name}} </span></div>
        <div>4</div>
        <div></div>
        <div>1</div>
        <div>2</div>
        <div>2.1</div>
        <div>2.2 </div>
        <div></div>
        <div>1</div>
        <div>2</div>
        <div>3</div>
        <div class='row_center'>
          <span class='display'>
             
            <span class="underline"> {{test.name}} </span>
            <div class='margin-30px'></div>
            <div>
              <span class="underline"> {{test.name}} </span>
              <span class="underline"> {{test.name}} </span>
              <span class="underline"> {{test.name}} </span>
            </div>
          </span>
          <span class='display'>
             
            <span class="underline"> {{test.name}} </span>
            <div class='margin-30px'></div>
            <div>
              <span class="underline"> {{test.name}} </span>
              <span class="underline"> {{test.name}} </span>
              <span class="underline"> {{test.name}} </span>
            </div>
          </span>
        </div>

        <div class='margin-t-20px'>
          <span class="underline"> {{test.name}} </span>
        </div>
      </div>
    </div>

    <el-dialog title="" :visible.sync="tartInfo" class='dialog' @close="cancelTartInfo">
      <el-form>
        <el-form-item label="" class='select'>
          <el-select v-model="disabled_state" placeholder="">
            <el-option v-for="item in tartOptions" :key="item.id" :label="item.param" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-input v-model="break_desc" class='textarea' type="textarea" />
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelTartInfo"> </el-button>
        <el-button type="primary" @click="check"> </el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
import contractExamine from "@/modules/PurchaseContract/contractExamine";
import maintainList from "@/modules/PurchaseContract/maintainList";
export default {
  data() {
    return {
      searchObj: {
        search: "",
        tag_search: "",
        page: 1
      },
      tartInfo: false,
      tipActiveIndex: "",
      pageSize: 0,
      total: 0,
      tableData: [],
      operationType: 0, //0  1 2 3  4
      store_id: "",
      test: {
        name: ""
      },
      disabled_state: "", //
      break_desc: "",
      sub_id: "",
      tartOptions: []
    };
  },
  created() {
    if (this.$route.query.tag_search) {
      this.searchObj.tag_search = this.$route.query.tag_search;
      this.tipActiveIndex = this.searchObj.tag_search;
    }
  },
  mounted() {
    if (this.searchObj.tag_search == 0) {
      this.getContractExList(); //
    } else if (this.searchObj.tag_search == 1) {
      this.getContractList(); //
    }
  },
  methods: {
    //
    async tart(row, type) {
      this.tartInfo = true;
      this.sub_id = row.sub_id;
      this.getBreakType();
    },
    //  
    async getBreakType() {
      let res = await this.api.getBreakType();
      if (res.code == 200) {
        this.tartOptions = res.data;
      }
    },
    async check() {
      let temp = {};
      temp.break_desc = this.break_desc;
      temp.sub_id = this.sub_id;
      temp.disabled_state = this.disabled_state;
      let res = await this.api.break(temp);
      if (res.code == 200) {
        this.$message({
          type: "success",
          message: "!"
        });
      }
      this.cancelTartInfo();
      this.getContractList();
    },
    cancelTartInfo() {
      this.disabled_state = '';
      this.break_desc = "";
      this.tartInfo = false;
    },
    //  //
    examine(row, type) {
      this.$router.push({
        name: "addPurchaseContract",
        query: {
          operationType: type,
          tag_search: this.searchObj.tag_search,
          sub_id: row.sub_id
        }
      });
    },
    async edit(row, type) {
      this.$router.push({
        name: "previewInfo",
        query: {
          operationType: type,
          store_id: row.store_id
        }
      });
    },
    // showAdd(type) {
    //   //0
    //   this.$router.push({
    //     name: "addPurchaseContract",
    //     query: {
    //       operationType: type
    //     }
    //   });
    // },
    search() {
      this.searchObj.page = 1;
      this.getContractExList();
    },
    async getContractExList() {
      let res = await this.api.getContractExList(this.searchObj);
      if (res.code == 200) {
        this.tableData = res.data.data;
        this.total = res.data.total;
        this.pageSize = res.data.per_page;
      }
    },
    async getContractList() {
      let res = await this.api.getContractList(this.searchObj);
      if (res.code == 200) {
        this.tableData = res.data.data;
        this.total = res.data.total;
        this.pageSize = res.data.per_page;
      }
    },
    clickTip(index) {
      this.searchObj.tag_search = index;
      let href = location.href;
      let p = href.indexOf("?");
      if (p != -1) {
        href = href.substring(0, p);
      }
      location.href = href + "?tag_search=" + this.searchObj.tag_search;
      this.tipActiveIndex = index;
      if (index == 0) {
        this.getContractExList();
      } else if (index == 1) {
        this.getContractList();
      }
    },
    pageChange(page) {
      this.searchObj.page = page;
      this.getContractExList();
    },
    //
    printing(row, type) {
      console.log(row);
      this.test.name = 123;

      this.$nextTick(() => {
        //
        let old = document.body.innerHTML;

        let htmlTest = document.querySelector(".printExcel");
        let content = htmlTest.innerHTML;

        //
        document.body.innerHTML = content;
        window.print();
        //
        window.location.reload();

        //
        // document.body.innerHTML = old;
      });
    }
  },
  components: {
    contractExamine,
    maintainList
  }
};
</script>





