<style lang="less" scoped>
</style>
<template>
  <div>
    <el-table :data="tableData" border style="width: 100%" class="mt-30">
      <el-table-column prop="" label="" align='center' width="70px">
        <template slot-scope="scope">{{getIndex(scope)}}</template>
      </el-table-column>
      <el-table-column prop="sub_code" label="" align='center' width="90px"></el-table-column>
      <el-table-column prop="house_code" label="" align='center'></el-table-column>
      <el-table-column prop="project_name" label="" align='center'></el-table-column>
      <el-table-column prop="store_name" label="" align='center'></el-table-column>
      <el-table-column prop="agent_name" label="" align='center' width="90px"></el-table-column>
      <el-table-column prop="agent_tel" label="" align='center' width="100px"></el-table-column>
      <el-table-column label="" align='center' width="90px">
        <template slot-scope="scope">{{scope.row.build_name + scope.row.unit_name + scope.row.house_name}}</template>
      </el-table-column>
      <!-- <el-table-column prop="build_area" label="()" align='center' width="110px"></el-table-column> -->
      <el-table-column prop="total_price" label="()" align='center' width="110px"></el-table-column>
      <el-table-column prop="break_money" label="()" align='center' width="90px"></el-table-column>
      <el-table-column prop="earnest_money" label="()" align='center' width="90px"></el-table-column>
      <el-table-column prop="appoint_construct_time" label="" align='center' width="100px"></el-table-column>
      <el-table-column prop="disabled_state" label="" align='center' width="150px"></el-table-column>
      <el-table-column prop="operation" label="" align='center' width="140px">
        <template slot-scope="scope">
          <el-button type="text" @click='printing(scope.row,1)'></el-button>
          <el-button type="text" @click='examine(scope.row,2)'></el-button>
          <el-button type="text" @click='tart(scope.row,3)'></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  props: ["tableData"],
  methods: {
    getIndex(row) {
      let index = row.$index + 1;
      return index;
    },
    examine(row, type) {
      this.$emit("examine", row, type);
    },
    printing(row, type) {
      this.$emit("printing", row, type);
    },
    tart(row, type) {
      this.$emit("tart", row, type);
    }
  }
};
</script>


