<style lang="less" scoped src="./index.less" ></style>
<style lang="less">
body {
  background-color: #fafafc;
}
.companyArriveOnVisit {
  .el-table thead {
    color: #333;
  }
  .el-form-item__label {
    float: none;
  }
  .el-dialog__body {
    padding-top: 0;
  }
  .el-table--border,
  .el-table--group {
    margin-top: 5px;
  }
  .el-table th {
    padding: 8px 0px;
    color: #333;
  }
  .el-table td {
    padding: 6px 0;  // 0
    color: #333;
  }
}
</style>
<template>
  <div class='companyArriveOnVisit'>
    <div class='title'>

      <div class='tab-block'>
        <div class='text1'>  </div>
        <div class="search-block">
          <div class="time_block">
            <el-date-picker v-model="searchObj.start_time" type="date" placeholder="" class='date'> </el-date-picker>
            <span class='date_text'></span>
            <el-date-picker v-model="searchObj.end_time" type="date" placeholder="" class='date'> </el-date-picker>
          </div>
          <el-input class='query' placeholder="//" v-model="searchObj.search"></el-input>
          <el-button icon="el-icon-search" circle @click="getVisitList"></el-button>
        </div>
        <div class='btn'>
          <a download="" :href="exportExcel()" target="_black" class='export'></a>
        </div>
      </div>
      <div>
        <el-button class='tip' :class="tipActiveIndex==0?'active':''" type="text" @click="clickTip(0)"></el-button>
        <el-button class='tip' :class="tipActiveIndex==1?'active':''" type="text" @click="clickTip(1)"></el-button>
        <el-button class='tip' :class="tipActiveIndex==2?'active':''" type="text" @click="clickTip(2)"></el-button>
      </div>
    </div>
    <el-table :data="tableData" border style="width: 100%" class="mt-30">
      <el-table-column prop="" label="" align='center' width="70px">
        <template slot-scope="scope">{{getIndex(scope)}}</template>
      </el-table-column>
      <el-table-column prop="client_id" label="" align='center' width="90px"></el-table-column>
      <el-table-column prop="project_name" label="" align='center'></el-table-column>
      <el-table-column prop="name" label="" align='center' width="100px"></el-table-column>
      <el-table-column prop="tel" label="" align='center' width="120px"></el-table-column>
      <el-table-column prop="property_advicer_real" label="" align='center' width="100px"></el-table-column>
      <el-table-column prop="butter_name" label="" align='center' width="100px"></el-table-column>
      <el-table-column label="" align='center' width="200px">
        <template slot-scope="scope">{{scope.row.agent_name+ ' / ' +scope.row.agent_tel}}</template>
      </el-table-column>
      <el-table-column prop="visit_time" label="" align='center' width="180px"></el-table-column>
      <el-table-column label="" align='center' width="140px">
        <template slot-scope="scope">{{state(scope.row.current_state)}} / {{disabledState(scope.row.disabled_state)}}</template>
      </el-table-column>
      <!-- <el-table-column prop="operation" label="" align='center' width="100px">
        <template slot-scope="scope">
          <el-button type="text" @click='showProject(scope.row)'></el-button>
        </template>
      </el-table-column> -->
    </el-table>
    <el-pagination background class='page' layout="prev, pager, next" :page-size="pageSize" :current-page="searchObj.page" :total="total" @current-change="pageChange">
    </el-pagination>
  </div>
</template>
<script>
export default {
  data() {
    return {
      searchObj: {
        tag_search: 0,
        start_time: "",
        end_time: "",
        page: 1,
        search: "",
        type: ""
      },
      pageSize: 0,
      total: 0,
      tipActiveIndex: 0,
      tableData: [],
      project_id: "",
      rule_id: "",
      options: [],
      value: "",
      operationType: 0 //0   1 
    };
  },
  mounted() {
    this.getVisitList();
  },
  methods: {
    exportExcel() {
      let url =
        this.api.exportExcel +
        "/?type=" +
        2 +
        "&tag_search=" +
        this.searchObj.tag_search;
      return url;
    },
    clickTip(index) {
      this.tipActiveIndex = index;
      this.searchObj.tag_search = index;
      this.search();
    },
    search() {
      this.getVisitList();
    },
    async getVisitList() {
      if (this.searchObj.start_time && !this.searchObj.end_time) {
        this.$message({
          type: "error",
          message: ``
        });
      }
      let res = await this.api.getVisitList(this.searchObj);
      if (res.code == 200) {
        this.tableData = res.data.data;
        this.total = res.data.total;
        this.pageSize = res.data.per_page;
      }
    },
    search() {
      this.searchObj.page = 1;
      this.getVisitList();
    },
    getIndex(row) {
      let index = row.$index + 1 + (this.searchObj.page - 1) * this.pageSize;
      return index;
    },
    pageChange(page) {
      this.searchObj.page = page;
      this.getVisitList();
    },
    state(row) {
      if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      } else if (row == 3) {
        return "";
      } else if (row == 4) {
        return "";
      } else if (row == 5) {
        return "";
      } else if (row == 6) {
        return "";
      }
    },
    disabledState(row) {
      if (row == 0) {
        return "";
      } else {
        return "";
      }
    }
  }
};
</script>





