<style lang="less" scoped src="../index.less"></style>

<style lang="less">
.AddProject {
  width: 1024px;
  margin: 0 auto;
  .el-table td,
  .el-table th {
    padding: 0;
    color: #333;
  }
  .el-form-item.is-error {
    padding-bottom: 20px;
  }
  .el-input__inner {
    line-height: 0px;
  }
  .el-form-item__label {
    float: none;
    color: #333;
  }
  .el-dialog__body {
    padding-top: 0;
  }
  .el-dialog {
    width: 63%;
    top: -10%;
  }
  .el-table--border,
  .el-table--group {
    margin-top: 20px;
  }
  .property_type {
    font-size: 14px;
    color: #333;
    line-height: 40px;
    padding: 0 10px 0 0;
  }
  .el-table th {
    padding: 0;
  }
  .cell {
    padding: 6px 0px;
  }
  .request {
    display: inline-block;
    text-align: right;
  }
  .title-top {
    padding: 30px 0;
    text-align: left;
    position: relative;
    top: 10px;
    font-size: 19px;
    .title-btn {
      position: absolute;
      top: 18px;
      right: 0;
    }
  }
}
</style>
<template>
  <div class="AddProject">
    <div class="AddPorject-wrapper">
      <div class="AddPorject-wrapper-inner">
        <div class='title-top'>
          <span></span>
          <span class="title-btn">
            <el-button type="primary" @click="submit" v-if="operationType==0||operationType==1"></el-button>
            <el-button @click="cancel"></el-button>
          </span>
        </div>
        <el-form :model="form" :rules="rules" ref="ruleForm" class='form'>
          <el-form-item label="" class='input' prop="project_name">
            <el-input v-model="form.project_name" auto-complete="off" :disabled="isDisable()" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="province">
            <!--  -->
          </el-form-item>
          <el-form-item class='input' prop="absolute_address">
            <!-- <el-input id="suggestId" v-model="form.absolute_address" auto-complete="off" class='input-1' :disabled="isDisable()" placeholder=""></el-input> -->
            <map-tool-input v-model="form.absolute_address" :area="area" ref="mapToolInput" @change="addressChange" :disabled="isDisable()" />
          </el-form-item>
          <!--  -->
          <map-tool class="map-tool" ref="mapTool" @load="mapLoad" :longitude.sync="form.longitude" :latitude.sync="form.latitude" />

          <!--  -->
          <div class="property_type"></div>
          <el-form-item prop="property_type">
            <el-checkbox :disabled="isPropertyTypeDisabled(item)" v-model="form.property_type" v-for="item in typeOptions" :key="item.param_id" :label="item.param_id">{{item.param}}</el-checkbox>
          </el-form-item>

          <el-form-item label="" prop="developer_name" class='input1'>
            <el-input v-model="form.developer_name" auto-complete="off" placeholder="" :disabled="operationType===2"></el-input>
          </el-form-item>
          <el-form-item label="" prop="company_relation" class='select'>
            <el-radio-group v-model="form.company_relation" :disabled="operationType===2">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" prop="statement_company" class='input'>
            <el-input :disabled="operationType===2" v-model="form.statement_company" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="project_hold_name" class='input'>
            <el-input :disabled="operationType===2" v-model="form.project_hold_name" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="project_hold_phone" class='input'>
            <el-input :disabled="operationType===2" v-model="form.project_hold_phone" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" class='textarea' v-if='!operationType==1'>
            <el-input :disabled="operationType===2" v-model="form.remark" type="textarea"></el-input>
          </el-form-item>
        </el-form>
        <div class='tableIn-btn'>
          <el-button type="primary" v-if="operationType==0||operationType==1" @click='showUser'></el-button>
        </div>
        <!--  -->
        <el-table :data="form.project_user" border>
          <el-table-column property="account" label="" align='center'></el-table-column>
          <el-table-column property="name" label="" align='center'></el-table-column>
          <!-- <el-table-column property="phone" label="" align='center'></el-table-column> -->
          <el-table-column property="state" label="" align='center' v-if="operationType==1">
            <template slot-scope="scope">
              {{showState(scope.row.state)}}
            </template>
          </el-table-column>
          <el-table-column property="operation" v-if="operationType == 0 || operationType == 1" label="" align='center'>
            <template slot-scope="scope">
              <el-button type="text" @click='editUser(scope.row , scope.$index)'></el-button>
              <el-button type="text" v-if="operationType == 0" @click='removeUser(scope.$index)'></el-button>
              <el-button type="text" @click='enabledDisable(scope.row)' v-if="!operationType == 0">{{StateBtn(scope.row.state)}}</el-button>
            </template>
          </el-table-column>
        </el-table>
        <!--  -->
        <div>
          <el-upload class="uploadFile" :auto-upload="false" action="" :on-change="fileUpload">
            <el-button class="uploadBtn" ref="uploadBtn" size="small" type="primary" :show-file-list="false"></el-button>
          </el-upload>
        </div>
        <div class='tableIn-btn' v-if="operationType == 0">
          <el-button type="primary" @click='showUploadFile'></el-button>
        </div>
        <el-table :data="form.project_agreement" border>
          <el-table-column property="file_name" label="" align='center'></el-table-column>
          <el-table-column label="" align='center'>
            <template slot-scope='scope'>
              <a target="_blank" :href="base+scope.row.url"></a>
            </template>
          </el-table-column>
          <el-table-column property="create_name" label="" align='center'></el-table-column>
          <el-table-column property="create_time" label="" align='center'></el-table-column>
        </el-table>
        <div v-if="form.source == 1 && ( auditing_info.auditing_state==0 || auditing_info.auditing_state==1)">
          <div class='num_set'></div>
          <el-form :model="form" class='form'>
            <el-form-item label="" class='inputAud'>
              <el-input v-model="auditing_info.auditing_name" auto-complete="off" :disabled="isDisable()"></el-input>
            </el-form-item>
            <el-form-item label="" class='inputAud'>
              <el-input v-model="auditing_info.auditing_time" auto-complete="off" :disabled="isDisable()"></el-input>
            </el-form-item>
            <el-form-item label="" class='inputAud state'>
              {{auditingState(auditing_info.auditing_state)}}
            </el-form-item>
            <el-form-item label="" class='textarea'>
              <el-input type="textarea" v-model="auditing_info.auditing_remark" :disabled="isDisable()"></el-input>
            </el-form-item>
          </el-form>
        </div>
        <div v-if="auditing_info.auditing_state==1">
          <div class='num_set'></div>
          <el-form v-model="authentication_info" class='form'>
            <el-form-item label="" class='inputAud'>
              <el-input v-model="authentication_info.deposit" auto-complete="off" :disabled="isDisable()"></el-input>
            </el-form-item>
            <el-form-item label="" class='inputAud'>
              <el-input v-model="authentication_info.no_price" auto-complete="off" :disabled="isDisable()"></el-input>
            </el-form-item>
            <el-form-item label="" class='inputAud'>
              <el-input v-model="authentication_info.allow" auto-complete="off" :disabled="isDisable()"></el-input>
            </el-form-item>
            <el-form-item label="" class='inputAud state'>
              {{projectState(authentication_info.auth_state)}}
            </el-form-item>
            <el-form-item class='request'>
              <el-button type="primary" @click="requestRefund()" :disabled="operationType===2" v-if='authentication_info.deposit>0'></el-button>
            </el-form-item>
            <div class='num_details'></div>
            <el-table :data='authentication_info.business_log' border>
              <el-table-column label="" align='center' width="70px">
                <template slot-scope="scope">{{getIndex(scope)}}</template>
              </el-table-column>
              <el-table-column property="drawee" label="" align='center'></el-table-column>
              <el-table-column property="payee" label="" align='center'></el-table-column>
              <el-table-column property="price" label="" align='center'></el-table-column>
              <el-table-column property="contact_name" label="" align='center'></el-table-column>
              <el-table-column property="contact_phone" label="" align='center' width="120px"></el-table-column>
              <el-table-column prop="type" label="" align='center'>
                <template slot-scope="scope">{{authenticationType(scope.row.type)}}</template>
              </el-table-column>
              <el-table-column property="create_name" label="" align='center'></el-table-column>
              <el-table-column property="create_time" label="" align='center' width="150px"></el-table-column>
              <el-table-column property="operation" label="" align='center' width="80px">
                <template slot-scope="scope">
                  <el-button type="text" @click="seeRequestRefund(scope.row)"></el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-form>
        </div>
        <div v-if="auditing_info.auditing_state==1">
          <div class='num_set'></div>
          <el-table :data="project_history" border>
            <el-table-column label="" align='center' width="70px">
              <template slot-scope="scope">{{getIndex(scope)}}</template>
            </el-table-column>
            <el-table-column property="company_name" label="" align='center'></el-table-column>
            <el-table-column property="company_relation" label="" align='center'></el-table-column>
            <el-table-column property="s_time" label="" align='center'></el-table-column>
            <el-table-column property="e_time" label="" align='center'></el-table-column>
            <el-table-column property="project_hold_name" label="" align='center'></el-table-column>
            <el-table-column property="project_hold_phone" label="" align='center'></el-table-column>
          </el-table>
        </div>
        <!--  -->
        <el-dialog title="" :visible.sync="dialogFormVisibleAccounts" class='tableUser' @close="cancelUser">
          <el-form :model="projectUserForm" :rules="userFormRules" ref="projectUserForm">
            <el-form-item label="" prop="account" class='input'>
              <el-input v-model="projectUserForm.account" auto-complete="off" placeholder=""></el-input>
            </el-form-item>
            <el-form-item v-show="isAccountValid" label="" prop="password" class='input'>
              <el-input v-model="projectUserForm.password" auto-complete="off" type='password' placeholder=""></el-input>
            </el-form-item>
            <el-form-item v-show="!isAccountValid" label="" class='input'>
              <el-input v-model="projectUserForm.password" auto-complete="off" type='password' placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="name" class='input'>
              <el-input v-model="projectUserForm.name" auto-complete="off" placeholder=""></el-input>
            </el-form-item>
            <!-- <el-form-item label="" prop="phone" class='input'>
              <el-input v-model="projectUserForm.phone" auto-complete="off" placeholder=""></el-input>
            </el-form-item> -->

          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click='cancelUser'> </el-button>
            <el-button type="primary" @click='submitUser'> </el-button>
          </div>
        </el-dialog>
        <!--  -->
        <!-- <el-dialog  title="" :visible.sync="refundShow"  class='tableRefund' @close="">
    <div>=--</div>
    <div>9000=10000-1000-0</div>
      <el-button type='text' class='text'></el-button>
     <el-table :data="paymentRecord" border>
              <el-table-column property="nub" label="" align='center'></el-table-column>
              <el-table-column property="drawee" label="" align='center'></el-table-column>
              <el-table-column property="payee" label="" align='center'></el-table-column>
              <el-table-column property="price" label="" align='center'></el-table-column>
              <el-table-column property="create_time" label="" align='center'></el-table-column>
     </el-table>
     <div>{{}}</div>
  </el-dialog> -->
      </div>
    </div>

  </div>
</template>


<script>
import CitySelector from "../../../components/CitySelector";
export default {
  data() {
    return {
      rules: {
        project_name: [
          {
            required: true,
            message: ",2-20",
            change: "change",
            max: 20,
            min: 2
          }
        ],
        property_type: [
          {
            type: "array",
            required: true,
            message: "",
            change: "change"
          }
        ],
        statement_company: [
          {
            required: true,
            message: ",2-20",
            change: "change",
            max: 20,
            min: 2
          }
        ],
        province: [
          { required: true, message: "", change: "change" }
        ],
        absolute_address: [
          { required: true, message: "", change: "change" }
        ],
        developer_name: [
          {
            required: true,
            message: ",2-20",
            change: "change",
            max: 20,
            min: 2
          }
        ],
        project_hold_name: [
          {
            required: true,
            message: ",2-5",
            change: "change",
            pattern: /[\u4E00-\u9FA5]{2,5}(?:[\u4E00-\u9FA5]{2,5})*/
          }
        ],
        project_hold_phone: [
          {
            required: true,
            message: "",
            change: "change",
            pattern: /^1[34578]\d{9}$/
          }
        ],
        company_relation: [
          { required: true, message: "", change: "change" }
        ]
      },
      userFormRulesOrigin: {
        name: [
          {
            pattern: /[\u4E00-\u9FA5]{2,5}(?:[\u4E00-\u9FA5]{2,5})*/,
            required: true,
            message: "2-5",
            change: "change"
          }
        ],
        account: [
          {
            pattern: /^1[34578]\d{9}$/,
            required: true,
            message: "",
            change: "change"
          }
        ],
        password: [
          {
            required: true,
            message: " 6-16 ",
            change: "change",
            min: 6,
            max: 16
          }
        ]
      },
      form: {
        district: "",
        province: "",
        city: "",
        statement_company: "",
        project_hold_phone: "",
        latitude: "",
        longitude: "",
        project_name: "",
        absolute_address: "",
        property_type: [],
        developer_name: "",
        project_hold_name: "",
        company_relation: "",
        remark: "",
        project_id: "",
        check_state: "",
        project_agreement: [],
        project_user: []
      },

      map: null,
      sendata: {},
      gridData: [],
      historicalRecords: [],
      UserShow: [],
      Enclosure: [],
      dialogFormVisibleAccounts: false,
      showMap: false,
      isUserEdit: false,
      refundShow: false,
      isEditProjectList: false,
      paymentRecordTable: false,
      auditOptions: [],
      projectUserForm: {
        name: "",
        account: "",
        password: "",
        phone: "",
        state: 1
      },
      auditing_info: {
        auditing_name: "",
        auditing_time: "",
        auditing_remark: "",
        auditing_state: ""
      },
      paymentRecord: [],
      refund: [],
      userObj: {},
      fileObject: {},
      operationType: 0, //0   1   2 
      project: [],
      typeOptions: [],
      haveTypeOptions: [],
      project_history: [],
      projectHistory: {
        company_id: "",
        s_time: "",
        e_time: "",
        project_hold_phone: "",
        project_hold_name: "",
        company_relation: "",
        project_id: "",
        company_name: ""
      },
      authentication_info: {
        business_log: []
      },
      businessLogForm: {
        deposit: "",
        authentication_state: "",
        no_price: "",
        business_log: [
          {
            payee: "",
            drawee: "",
            contact_name: "",
            contact_phone: "",
            create_name: "",
            create_time: "",
            price: "",
            type: ""
          }
        ]
      },
      projectAgreement: {
        file_name: "",
        url: "",
        create_name: "",
        create_time: ""
      },
      userIndex: 0,
      area: ""
    };
  },
  computed: {
    userFormRules() {
      if (
        this.projectUserForm.password == "" &&
        this.operationType == 1 &&
        this.isUserEdit == true
      ) {
        let temp = Object.assign({}, this.userFormRulesOrigin);
        this.isAccountValid = false;
        temp.password = undefined;
        return temp;
      } else if (this.isUserEdit == true || this.isUserEdit == false) {
        let temp = Object.assign({}, this.userFormRulesOrigin);
        this.isAccountValid = true;
        return temp;
      }
    }
  },
  mounted() {
    this.operationType = this.$route.params.operationType;
    if (this.operationType === undefined) {
      this.$router.push({ name: "project" });
      return;
    }
    this.form.project_id = this.$route.params.project_id;
    if (this.form.project_id) {
      this.getProjectInfo();
    }
    this.getType();
  },
  methods: {
    getIndex(row) {
      let index = row.$index + 1;
      return index;
    },
    async requestRefund() {
      this.$router.push({
        name: "requestRefund",
        params: {
          operationType: this.$route.params.operationType,
          project_id: this.$route.params.project_id,
          allow: this.authentication_info.allow,
          info_id: this.form.info_id
        }
      });
    },
    seeRequestRefund(row) {
      this.$router.push({
        name: "payRecord",
        params: {
          operationType: this.$route.params.operationType,
          project_id: this.$route.params.project_id,
          id: row.id,
          allow: this.authentication_info.allow
        }
      });
    },
    isDisable() {
      if (this.operationType == 0) {
        return false;
      } else {
        return true;
      }
    },
    isPropertyTypeDisabled(item) {
      if (this.operationType == 2) {
        return true;
      } else if (this.operationType == 0) {
        return false;
      } else if (this.operationType == 1 && this.form.check_state == 1) {
        return this.haveTypeOptions.includes(item.param_id);
      }
    },
    async getProjectInfo() {
      let res = await this.api.getProjectInfo({
        project_id: this.form.project_id
      });
      if (res.code == 200) {
        let temp = { ...res.data.project };
        temp.property_type = [];
        temp.project_user = res.data.project_admin;
        temp.project_agreement = res.data.project_agreement;
        this.authentication_info = res.data.authentication_info;
        this.auditing_info = res.data.auditing_info;
        this.project_history = res.data.project_history;
        this.haveTypeOptions = [];
        for (let type of res.data.project.property_type) {
          temp.property_type.push(type.property_tag_id);
          this.haveTypeOptions.push(type.property_tag_id);
        }
        Object.assign(this.form, temp);
        this.$nextTick(() => {
          if (temp.province.indexOf("") > -1) {
            this.area = temp.province_name + temp.district_name;
          } else {
            this.area =
              temp.province_name + temp.city_name + temp.district_name;
          }
          this.addressChange(temp.absolute_address);
        });
      }
    },
    submit() {
      this.$refs["ruleForm"].validate(async valid => {
        if (valid) {
          if (this.operationType == 1) {
            let temp = {};
            temp.project_id = this.form.project_id;
            temp.info_id = this.form.info_id;
            temp.property_type = this.form.property_type;
            temp.developer_name = this.form.developer_name;
            temp.company_relation = this.form.company_relation;
            temp.project_hold_name = this.form.project_hold_name;
            temp.project_hold_phone = this.form.project_hold_phone;
            temp.statement_company = this.form.statement_company;
            let res = await this.api.getUpdateProject(temp);
            if (res.code == 200) {
              this.$message({ type: "success", message: "" });
              setTimeout(() => {
                this.$router.push({ name: "project" });
              }, 2000);
            }
          } else {
            let res = await this.api.getCreateProject(this.form);
            if (res.code == 200) {
              this.$message({ type: "success", message: "" });
              setTimeout(() => {
                this.$router.push({ name: "project" });
              }, 2000);
            }
          }
        } else {
          return false;
        }
      });
    },

    cancel() {
      this.$router.push({ name: "project" });
    },

    async submitUser() {
      this.$refs["projectUserForm"].validate(async valid => {
        if (valid) {
          if (this.operationType == 1) {
            if (this.isUserEdit) {
              this.projectUserForm.project_id = this.form.project_id;
              let temp = Object.assign({}, this.projectUserForm);
              let res = await this.api.updateProjectAdmin(temp);
              if (res.code != 200) {
                return;
              }
            } else {
              this.projectUserForm.project_id = this.form.project_id;
              let temp = Object.assign({}, this.projectUserForm);
              let res = await this.api.addProjectAdmin(temp);
              if (res.code != 200) {
                return;
              }
            }
          }
          let temp = Object.assign({}, this.projectUserForm);
          if (!this.checkUserAccout(temp)) return;
          if (this.isUserEdit) {
            this.$message({
              type: "success",
              message: "!"
            });
            Object.assign(this.form.project_user[this.userIndex], temp);
          } else {
            this.$message({
              type: "success",
              message: "!"
            });
            this.form.project_user.push(temp);
          }
          this.cancelUser();
        } else {
          return false;
        }
      });
    },
    removeUser(index) {
      this.form.project_user.splice(index, 1);
    },
    cancelUser() {
      this.dialogFormVisibleAccounts = false;
      let originAccountsForm = this.$options.data()["projectUserForm"];
      Object.assign(this.projectUserForm, originAccountsForm);
    },
    showUser() {
      this.isUserEdit = false;
      this.projectUserForm.index = undefined;
      this.dialogFormVisibleAccounts = true;
    },
    editUser(row, index) {
      this.isUserEdit = true;
      this.dialogFormVisibleAccounts = true;
      Object.assign(this.projectUserForm, row);
      this.userIndex = index;
    },
    checkUserAccout(temp) {
      for (let i = 0; i < this.form.project_user.length; i++) {
        let user = this.form.project_user[i];
        if (i === this.userIndex) continue;
        if (user.account == temp.account) {
          this.$message({ type: "error", message: "" });
          return false;
        }
      }
      return true;
    },
    async enabledDisable(row) {
      let temp = {};
      temp.id = row.id;
      if (row.state == 1) {
        temp.state = 0;
      } else if (row.state == 0) {
        temp.state = 1;
      }
      let res = await this.api.updateAdminState(temp);
      if (res.code == 200) {
        this.getProjectInfo();
      }
    },
    showState(row) {
      if (row == 0) {
        return "";
      } else if (row == 1) {
        return "";
      }
    },
    StateBtn(row) {
      if (row == 1) {
        return "";
      } else if (row == 0) {
        return "";
      }
    },
    auditingState(row) {
      if (row == 0) {
        return "";
      } else if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      }
    },
    authenticationType(row) {
      if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      }
    },
    projectState(row) {
      if (row == 0) {
        return "";
      } else if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      } else if (row == 3) {
        return "";
      }
    },

    showUploadFile() {
      let btn = document.querySelector(".uploadBtn");
      btn.click();
    },

    async fileUpload(fileObj) {
      this.fileObject = fileObj;
      let flag = false;
      if (
        this.fileObject.name.indexOf(".docx") > -1 ||
        this.fileObject.name.indexOf(".xlcx") > -1 ||
        this.fileObject.name.indexOf(".doc") > -1 ||
        this.fileObject.name.indexOf(".jpg") > -1 ||
        this.fileObject.name.indexOf(".zip") > -1 ||
        this.fileObject.name.indexOf(".rar") > -1 ||
        this.fileObject.name.indexOf(".csv") > -1 ||
        this.fileObject.name.indexOf(".png") > -1 ||
        this.fileObject.name.indexOf(".pdf") > -1
      ) {
        flag = true;
      }
      if (!flag) {
        this.$message({
          type: "error",
          message:
            "docx,doc,xlcx,jpg,zip,rar,csv,png,pdf"
        });
        return;
      }
      let size = fileObj.size / 1024 / 1024;
      if (size > 5) {
        this.$message({
          type: "error",
          message: "5m"
        });
        return;
      }
      let file = this.fileObject.raw;
      let formData = new FormData();
      formData.append("url", file);
      let res = await this.api.uploadProjectAgreement(formData);
      if (res.code == 200) {
        let projectAgreementTemp = {};
        projectAgreementTemp.create_time = res.data.create_time;
        projectAgreementTemp.create_name = res.data.create_name;
        projectAgreementTemp.url = res.data.img_url;
        projectAgreementTemp.file_name = fileObj.name;
        this.form.project_agreement = [];
        this.form.project_agreement.push(projectAgreementTemp);
      }
    },

    // showMapDetails() {
    //   let row = document.querySelector(".map");
    //   row.classList.toggle("");
    // },

    auditStatus() {},
    sumbitRefund() {
      this.refundShow = true;
    },
    cancelRefund() {
      this.refundShow = false;
    },

    async getType() {
      let res = await this.api.getTags();
      if (res.code == 200) {
        this.typeOptions = res.data;
      }
    },
    mapLoad(map) {
      this.$refs["mapToolInput"].init(map);
    },
    areaChange(area) {
      this.area = area;
      this.$refs["mapTool"].focusArea(area);
    },
    addressChange(address) {
      this.form.absolute_address = address;
      let addStr = this.area + address;
      if (address) {
        this.$refs["mapTool"].focusAddress(addStr);
      } else {
        this.$refs["mapTool"].focusArea(this.area);
      }
    }
  },
  components: {
    CitySelector: CitySelector
  }
};
</script>




